import React from 'react';
import Style from './Home.module.scss';
import myimg from '../../img/myimg.jpeg'
import classNames from 'classnames';
import EmojiBullet from "./Emojibullet";
import CV from '../../Cv/Mycv.pdf'
import {Box} from "@mui/material";
import {info} from '../../info/info'
import { SocialIcon } from 'react-social-icons';
import { HiArrowDownTray } from "react-icons/hi2";

export default function Home() {

   return (

      
      <Box component={'main'} display={'flex'} flexDirection={{xs: 'column', md: 'row'}} alignItems={'center'}
           justifyContent={'center'} minHeight={'calc(100vh - 175px)'}>
         <Box className={classNames(Style.avatar, Style.shadowed)} alt={'image of developer'} style={{background: info.gradient}} component={'img'} src={myimg} width={{xs: '35vh', md: '40vh'}}
              height={{xs: '35vh', md: '40vh'}}
              borderRadius={'50%'} p={'0.75rem'} mb={{xs: '1rem', sm: 0}} mr={{xs: 0, md: '2rem'}}/>
         <Box>
            <h1>Hi, I'm <span style={{background: info.gradient, WebkitBackgroundClip: 'text', WebkitTextFillColor: 'transparent'}}>{info.firstName}</span><span className={Style.hand}></span>
            </h1>
            <h2>I'm {info.position}.</h2>
            <Box component={'ul'} p={'0.8rem'}>
               
               {info.miniBio.map((bio, index) => (
                  <EmojiBullet key={index} emoji={bio.emoji} text={bio.text}/>
               ))}
            </Box>
            <Box display={'flex'} gap={'1.5rem'} justifyContent={'center'} fontSize={{xs: '2rem', md: '2.5rem'}}>
         
             <SocialIcon url="https://twitter.com/Narashinhchau1" target='_blank'/>
            
              <SocialIcon url="https://facebook.com/profile.php?id=100041032547022" target='_blank'/>
            
              <SocialIcon url="https://instagram.com/__narashinhchaudhary__" target='_blank' />
            
             <SocialIcon url="https://github.com/narashinhchaudhary" target='_blank'/>
               
              <SocialIcon url="https://linkedin.com/in/narashinh-chaudhary-82850920b" target='_blank' />
            
               <a href={CV} download={document}><HiArrowDownTray /></a>
            
               
               
            </Box>
         </Box>
      </Box>
   )
}